<template>
    <div class="content_left">
        <ul class="ping" style="margin-top: 0;">
            <li v-for="item in messageAggre"
                :key="item.id"
                :style="{paddingBottom: item.reply.length > 0 ? '32px' : '10px'}">
                <div class="comment">
                    <span class="userName">{{item.userName}} : </span>
                    发表于
                    (<span>{{ timeTransformation(item.time) }}</span>)
                    <span class="reply" 
                      @click="replyDisplay(item.id, item.userName)"
                    >[回复]</span>
                    <p class="userComment">{{item.message}}</p>
                </div>
                <ul class="replay" :ref="`reply-${item.id}`">
                    <li v-for="k in item.reply" :key="k.id">
                        <span class="userName">{{k.userName}} : </span>
                        回复了
                        <span class="replyName">{{k.replyName}}</span>
                        (<span>{{ timeTransformation(item.time) }}</span>) 
                        <span class="reply"
                            @click="replyDisplay(item.id, k.userName)"
                        >[回复]</span>

                        <p class="userComment">{{k.replyContent}}</p>
                    </li>
                </ul>
                <div class="open" @click="replyBlock(`reply-${item.id}`, `hide-${item.id}`, `open-${item.id}`)"
                    :ref="`open-${item.id}`"
                    :style="{display: item.reply.length > 0 ? 'block' : 'none'}">
                    展开 ({{item.reply.length}}) 条回复
                </div>
                <div class="hide" @click="replyNone(`reply-${item.id}`, `open-${item.id}`, `hide-${item.id}`)"
                    :ref="`hide-${item.id}`">
                    隐藏回复
                </div>
            </li>
        </ul>
        <div class="comment">
            <form ref="form">
                <input type="text" placeholder="名称" id="userName">
                <div>
                    <input type="text" placeholder="请输入验证码" id="verification">
                    <span class="verification" v-html="verification.data"></span>
                    <span class="replace" @click="generateVerification">换一换</span>
                </div>
                <textarea placeholder="评论" id="message"></textarea>
                <button @click="publishMessage">发表留言</button>
            </form>
        </div>
        <div class="reply" v-show="replyBool" style="display: none;">
            <form ref="replyForm">
                <input type="text" placeholder="名称" id="userName">
                <div>
                    <input type="text" placeholder="请输入验证码" id="verification">
                    <span class="verification" v-html="verification.data"></span>
                    <span class="replace" @click="generateVerification">换一换</span>
                </div>
                <textarea placeholder="评论" id="replyContent"></textarea>
                <button @click="publishReply">发表回复</button>
            </form>
        </div>
        <div class="meng" v-show="replyBool" style="display: none;"></div>
    </div>
</template>

<script>
require("../static/css/article_index.css");
import tool from "../static/js/tool";
export default {
    data() { 
        return {
            // 所有留言的数据集合
            messageAggre: [],
            // 控制蒙层和回复输入框的显示与隐藏
            replyBool: false,
            // 保存将要回复的用户名称
            replyName: "",
            // 保存将要回复的评论id
            messageId: "",
            // 保存验证码
            verification: {},
        }
    },
    methods: {
        // 取出所有的留言
        getMessage() {
            tool.ajax(`/bok/getMessage`)
            .then( (data) => {
                console.log(data);
                this.messageAggre = data.data;
            } ) 
        },
        // 发表评论的事件函数
        publishMessage(e) {
            e.preventDefault();
            const userName = this.$refs.form.userName.value.trim();
            const message = this.$refs.form.message.value.trim();
            const verification = this.$refs.form.verification.value.toLowerCase().trim();
            if(!message || !userName) return;
            if(verification != this.verification.text.toLowerCase()) {
                this.$refs.form.verification.value = "";
                alert("验证码输入错误");
                return;
            }
            this.$refs.form.verification.value = "";
            this.$refs.form.userName.value = "";
            this.$refs.form.message.value = "";
            tool.ajax(`/bok/publishMessage?userName=${userName}&message=${message}`)
            .then( (data) => {
                // 发表成功后，重新再获取留言信息
                this.getMessage();
            } );
        },
        // 发表回复的事件函数
        publishReply(e) {
            e.preventDefault();
            const userName = this.$refs.replyForm.userName.value.trim();
            const replyContent = this.$refs.replyForm.replyContent.value.trim();
            const verification = this.$refs.replyForm.verification.value.toLowerCase().trim();
            if(!replyContent || !userName) return;
            if(verification != this.verification.text.toLowerCase()) {
                this.$refs.replyForm.verification.value = "";
                alert("验证码输入错误");
                return;
            }
            this.$refs.replyForm.verification.value = "";
            this.$refs.replyForm.userName.value = "";
            this.$refs.replyForm.replyContent.value = "";

            this.replyBool = false;

            const url = `/bok/publishMessageReply?userName=${userName}&replyContent=${replyContent}&replyName=${this.replyName}&messageId=${this.messageId}`;
            tool.ajax(url)
            .then( (data) => {
                // 发表成功后，重新再获取留言信息
                this.getMessage();
            } );
        },
        // 打开回复的输入框
        replyDisplay(messageId, replyName) {
            console.log(messageId, replyName);
            this.replyName = replyName;
            this.messageId = messageId;
            this.replyBool = true;
        },
        // 显示评论的回复区
        // 三个参数，对应回复区ref值(要显示)，对应显示隐藏回复区按钮的ref值(要显示)
        // 对应显示回复区按钮的ref值(要隐藏)
        replyBlock(replyRef, hideRef, openRef) {
            this.$refs[replyRef][0].style.display = "block";
            this.$refs[hideRef][0].style.display = "block";
            this.$refs[openRef][0].style.display = "none";
        },
        // 隐藏评论的回复区
        // 三个参数，对应回复区ref值(要隐藏)，对应显示隐藏回复区按钮的ref值(要隐藏)
        // 对应显示回复区按钮的ref值(要显示)
        replyNone(replyRef, openRef, hideRef) {
            this.$refs[replyRef][0].style.display = "none";
            this.$refs[openRef][0].style.display = "block";
            this.$refs[hideRef][0].style.display = "none";
        },
        // 时间转换，转换成xx-xx-xx的样式
        timeTransformation(time) {
            const t = new Date(time * 1000);
            const fullYear = t.getFullYear();
            const month = t.getMonth();
            const date = t.getDate();
            return `${fullYear}-${month + 1}-${date}`
        },
        // 随机生成一个验证码
        generateVerification() {
            tool.ajax("/bok/getErCode")
            .then( (data) => {
               this.verification = data;
            } )
        }
    },
    mounted() {
        // 得到当前文章有关的评论
        this.getMessage();
        this.generateVerification();
    }
}
</script>