<template>
    <div class="content_right">
        <div class="labelClo">
            <h4>标签云</h4>
            <div class="label">
                <span v-for="k in labelAggre" :key="k.id" 
                      :style="{color: 'rgb(255, 255, 255)', cursor: 'pointer'}">
                    <router-link :to="`/bok#label=${k.id}`">{{k.label}}</router-link>
                </span>
            </div>
        </div>
        <div class="popular">
            <h4>最新热门</h4>
            <ul>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
                <li><a href="#">这是一个链接</a></li>
            </ul>
        </div>
        <div class="latestComment">
            <h4>最新评论</h4>
            <ul>
                <li v-for="item in latestComment" :key="item.id">
                    <router-link :to="`/bok/article#articleId=${item.articleId}`">
                        <span class="userName">{{item.userName}}</span>
                        <span class="time">{{ transformationTime(item.time) }}</span>
                        <p>{{ item.comment }}</p>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
require("../static/css/contentRight.css");
import tool from "../static/js/tool"
export default {
    data() {
        return {
            // 标签云的数据集合
            labelAggre: [],
            // 保存最新评论的数据集合
            latestComment: [],
        }
    },
    methods: {
        // 获取标签云列表的函数
        getLabelCloList() {
            tool.ajax("/bok/getLabel")
            .then( (data) => {
                this.labelAggre = data.data;
            } )
        },
        // 获取最新的十条评论
        getLatestComment() {
            tool.ajax("/bok/getLatestComment?nub=10")
            .then( (data) => {
                this.latestComment = data.data;
            } )
        },
        // 更改时间的展示样式
        transformationTime(time) {
            return tool.transformationTime(time * 1000, "-");
        }
    },
    mounted() {
        this.getLabelCloList();
        this.getLatestComment();
    }
}
</script>