<template>
    <div class="content_left">
        <div class="every-day">
            <h6>每日一句</h6>
            <p> 
                {{everyDayAggre.everyDay}}
                <span>----{{everyDayAggre.authorName}}</span>
            </p>
        </div>
        <div class="article-list" id="article">
            <div class="article" v-for="k in dispalyArticle" :key="k.id">
                <router-link :to=" '/bok/article#articleId=' + k.id ">
                    <div class="title">{{k.title}}</div>
                    <p v-html="k.article"></p>
                </router-link>
                <div class="foot">
                    发布于
                    <span>{{k.time}}</span>；
                    浏览
                    (<span>{{k.clicks}}</span>)；
                    <span v-for="(item, index) in k.label" :key="index"
                          @click="labelMapArticle(item)"
                          :style="{color: 'rgb(156, 39, 176)', cursor: 'pointer'}"
                          >{{item}}</span>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import tool from "../static/js/tool";
export default {
    data() {
        return {
            // 每日一句的数据集合
            everyDayAggre: {
                everyDay: "",
                authorName: ""
            },
            // 所有文章的数据集合
            articleList: [],
            // 当前显示的文章的数据集合
            dispalyArticle: [],
        }
    },
    methods: {
        // 获取每日一句函数
        getEveryDay() {
            tool.ajax("/bok/getEveryDay")
            .then( (k) => {
                this.everyDayAggre = k;
            } )
        },
        // 获取文章列表的函数
        getArticleList() {
            // 获取到当前页面的锚点
            const hash = window.location.hash;
            if(!hash) {
                tool.ajax("/bok/getArticle")
                .then( (k) => {
                    // 解析时间，转换成xx-xx-xx的格式，解析标签依赖
                    for(let i = 0; i < k.data.length; i ++) {
                        const time = new Date(k.data[i].time * 1000);
                        k.data[i].time = `${time.getFullYear()}-${time.getMonth() + 1}-${time.getDate()}`
                        k.data[i].label = k.data[i].label.split(" ");
                    }
                    this.articleList = k.data;
                    this.dispalyArticle = this.articleList;
                } )
            }
            // 不是由关键词跳转过来的，关键词跳转有专门的数据申请处理
            else if(hash !== "#keyword") {
                // 分解锚点
                const hashAggre = tool.analysisHash(hash);

                // 取出对应标签id，对应的标签值
                // 首先要先获取到所有的标签
                tool.ajax("/bok/getLabel")
                .then( (data) => {
                    const a = data.data;
                    for(let i = 0; i < a.length; i ++) {
                        // 表示当前标签的id，与对应的锚点值相同
                        if(hashAggre["label"] == a[i].id) {
                            // 请求标签的对应文章
                            this.labelMapArticle(a[i].label);
                        }
                    }
                } )
            }
        },
        // 根据标签云获取对应的文章，标签的点击事件函数
        labelMapArticle(label) {
            tool.ajax(`/bok/getLabelMapArticle?label=${label}`)
            .then( (k) => {
                // 解析时间，转换成xx-xx-xx的格式，解析标签依赖
                for(let i = 0; i < k.data.length; i ++) {
                    const time = new Date(k.data[i].time * 1000);
                    k.data[i].time = `${time.getFullYear()}-${time.getMonth() + 1}-${time.getDate()}`
                    k.data[i].label = k.data[i].label.split(" ");
                }
                this.articleList = k.data;
                this.dispalyArticle = this.articleList;
            } )
        },
        // 根据关键词查询文章信息
        keywordGetArticle (keyword) {
            tool.ajax(`/bok/keywordGetArticle?keyword=${keyword}`)
            .then( (data) => {
                console.log(data.data);
                // 所有文章的数据集合
                this.articleList = data.data;
                // 当前显示的文章的数据集合
                this.dispalyArticle = data.data;
            } )
        }
    },
    mounted() {
        this.getArticleList();
        this.getEveryDay();
        // 注册事件总线上的方法，供其他组件向该组件传参
        this.bus.$on("keyGetArticle", this.keywordGetArticle);
    },
    // 路径发生变化重新更新文章，表示点击了标签，进行文章的筛选
    beforeRouteUpdate(to, from, next) {
        // 先进行跳转，然后在更新文章列表
        next();
        // 重新获取文章列表
        this.getArticleList();
    }
}
</script>

