export default {
    ajax: async function(url) {
        const k = await fetch(url);
        const data = await k.json();
        return data;
    },
    analysisHash(hash) {
        const arr = hash.substr(1).split("&");
        const hashAggre = {};
        for(let i = 0; i < arr.length; i ++) {
            const aggre = arr[i].split("=");
            hashAggre[ aggre[0] ] = aggre[1]
        }
        return hashAggre;
    },
    transformationTime(time, join) {
        const t = new Date(time);
        const fullYear = t.getFullYear();
        const month = t.getMonth() + 1;
        const date = t.getDate();
        return [fullYear, month, date].join(join);
    }
}
