<template>
    <div class="container">
        <a href="/" class="title">我的个人博客 | 技术博客</a>
        <ul class="menu">
            <li><router-link to="/bok/">首页</router-link></li>
            <li class="drop" @click="caseDrotBlock(`Note`)">
                <span>笔记</span>
                <div class="down" v-if="caseDrotBooNote" style="width: 150px;">
                    <a href="#">js知识点</a>
                    <a href="#">css知识点</a>
                    <a href="#">vue知识点</a>
                    <a href="#">scss知识点</a>
                </div>
            </li>
            <li class="drop" @click="caseDrotBlock(`Case`)">
                <span>案例</span>
                <div class="down" v-if="caseDrotBooCase">
                    <router-link to="/bok/case/project">小项目</router-link>
                    <router-link to="/bok/case/game">小游戏</router-link>
                    <router-link to="/bok/case/demo">小demo</router-link>
                </div>
            </li>
            <li><a href="#">关于</a></li>
            <li><router-link to="/bok/message">留言</router-link></li>
        </ul>
        <div class="search-bar">
            <input type="text" placeholder="请输入关键字查找" ref="keyword">
            <div class="btn" @click="keyword">搜索</div>
        </div>
    </div>
</template>


<script>
require("../static/css/base.css");
export default {
    data() {
        return {
            caseDrotBooCase: false,
            caseDrotBooNote: false
        }
    },
    methods: {
        keyword() {
            // 先跳转到主页
            this.$router.history.push("/bok#keyword");
            const keyword = this.$refs.keyword.value.trim();
            // 把关键词信息发送到home组件中
            this.bus.$emit("keyGetArticle", keyword);
        },
        caseDrotBlock(type) {
            if( this[`caseDrotBoo` + type] ) {
                this[`caseDrotBoo` + type] = false;
            }else {
                this[`caseDrotBoo` + type] = true;
            }
        }
    }
}
</script>