<template>
    <div class="content_left">
        <ul class="case">
            <li v-for="item in caseAggre" :key="item.id">
                <a :href="item.href">
                    <img :src="item.imgSrc">
                    <div>
                        <h4>{{item.title}}</h4>
                        <p>{{item.intreduce}}</p>
                    </div>
                </a>
            </li>
        </ul>
    </div>
</template>


<script>
import "../static/css/case.css";
import tool from "../static/js/tool";
export default {
    data() {
        return {
            caseAggre: []
        }
    },
    methods: {
        getCase(type) {
            tool.ajax(`/bok/getCase?type=${type}`)
            .then( (data) => {
                this.caseAggre = data.data;
            } )
        }
    },
    mounted() {
        const type = this.$router.history.current.params.id;
        this.getCase(type);
    },
    beforeRouteUpdate(to, from, next) {
        next();
        const type = this.$router.history.current.params.id;
        this.getCase(type);
    }
}
</script>